
import { useEffect } from 'react'
import { Outlet, useLocation } from 'react-router-dom'
import Background from '../assets/background.png'
import PhoneBackground from '../assets/phone/background.png'

import '../styles/in_app_component.scss'

export default function InAppComponent(){

    const location = useLocation(),
        location_dict:object = {
            profile: ' ',
            games: ' ',
            scan_qr: ' QR ',
            mobile_profile: '   ?',
            mobile_waiting: ' ',
            mobile_game: ' ',
            mobile_sorting: '',
            connections: '',
            remove_excess: ' ',
            mobile_final_round: ' '
        },
        location_label:object = {
            game_start: '1- ',
            question: '1- ',
            mobile_waiting: '5/6',
            mobile_game: '1- ',
            mobile_sorting: '1- ',
            connections: '3- ',
            remove_excess: '2- ',
            mobile_final_round: '1- ',
            final_round: '3- ',
            results: '3- ',
            result_tour: '1- ',
            round_end: '1- '
        },
        pathname = location.pathname.replace('/', '') as any
        useEffect(() => {
            let logo_elem = document.querySelector('.logo_elem') as HTMLElement,
                adaptive_menu = document.querySelector('.adaptive_menu') as HTMLElement,
                score = document.querySelector('.score') as HTMLElement
            
            const updateWindowDimensions = () => {
                if(window.innerWidth < 1024){
                    if(logo_elem) logo_elem.style.display = 'none'
                    if(adaptive_menu) adaptive_menu.style.display = 'none'
                    if(score) score.style.display = 'flex'
                }else{
                    if(score) score.style.display = 'none'
                    if(adaptive_menu) adaptive_menu.style.display = 'none'                    
                    if(logo_elem) logo_elem.style.display = 'flex'
                }            
            }
    
            updateWindowDimensions()
            window.addEventListener("resize", updateWindowDimensions);
            return () => window.removeEventListener("resize", updateWindowDimensions) 
    
        }, [window.innerWidth])

    return(
        <div className='in_app_components' style={window.innerWidth > 768 ? { backgroundImage: `URL(${Background})`} : { backgroundImage: `URL(${PhoneBackground})`}}>
            <div className='banner_container'>
                <div className='banner'>
                    <h1>{location_dict[pathname as keyof object] ? location_dict[pathname as keyof object]  : 'Classic game' }</h1>
                </div>
                <h1 className='tour'>{location_label[pathname as keyof object] ? location_label[pathname as keyof object]  : '' }</h1>
            </div>
            <div className='content'>
                <Outlet />      
            </div>
        </div>
    )
}